<template>
  <div class="main__week-day">
    <h3 class="main__week-title">{{getWeekDay}}</h3>
    <p class="main__week-date">{{ getDay }} {{ getMonth }}</p>
            <img src="@/assets/img/sun.svg" alt="" v-if="description == 'ясно'">
            <img src="@/assets/img/cloud.svg" alt="" v-else-if="description == 'пасмурно'">
            <img src="@/assets/img/minirain.svg" alt="" v-else-if="description == 'небольшой дождь'">
            <img src="@/assets/img/sunrain.svg" alt="" v-else-if="description == 'облачно с прояснениями'">
            <img src="@/assets/img/minicloud.svg" alt="" v-else-if="description == 'небольшая облачность'">
            <img src="@/assets/img/minicloud.svg" alt="" v-else-if="description == 'переменная облачность'">
            <img src="@/assets/img/rain.svg" alt="" v-else-if="description == 'дождь'">
    <p class="main__week-temp">{{Math.round(day.temp.day)}}°</p>
    <p class="main__week-weather">{{day.weather[0].description}}</p>
  </div>
</template>

<script>
  import unix from "@/timestamp"
export default {
  props:{
    day:Object,
    
  },
  computed:{
    description(){
      return this.day.weather[0].description
    },
    getWeekDay(){
      return unix(this.day.dt, "weekday")
    },
    getDay(){
      return unix(this.day.dt, "day")
      
    },
    getMonth(){
      return unix(this.day.dt, "month")
      
    }
  }
}
</script>

