import { createStore } from 'vuex'
import axios from 'axios'
import moment from 'moment-timezone'

export default createStore({
  state: {
    apiKey: '658767b0ae936b022f59a69f44868419',
    getFullWeather: null
  },
  getters: {
    getFullWeather: state => state.getFullWeather,
    getDailyWeather: state => state.getFullWeather ? state.getFullWeather.daily : [],
    getCityName: state => state.getFullWeather ? state.getFullWeather.name : '',
    getLocalTime: (state) => {
      if (state.getFullWeather) {
        const timezoneOffset = state.getFullWeather.timezone_offset; // Временная зона в секундах
        const currentTime = moment().utcOffset(timezoneOffset / 60).format('HH:mm');
        return currentTime;
      }
      return null;
    }
  },
  mutations: {
    getWeather(state, payload){
        state.getFullWeather = payload
    }
  },
  actions: {
    async getWeather({commit, state}, city){
      try {
        let result = await axios.get(`https://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`)
        let weather = result.data[0]
        let {lat, lon, name} = weather
        let result2 = await axios.get(`https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&exclude=hourly,alerts,minutely&appid=${state.apiKey}&units=metric&lang=ru`)
        let weatherInfo = result2.data;
        let weatherObj = {...weatherInfo, name: name} 
        commit('getWeather', weatherObj)
        console.log(weatherObj);
      } catch (error) {
        console.log(error);
      }
    }
  },
  modules: {
  }
})
