<template>
  <div class="main__content">
    <div class="main__content-left">
      <p class="main__content-degree">
        {{ Math.round(getFullWeather.current.temp) }}°
      </p>
      <p class="main__content-today">Сегодня</p>
      <p class="main__content-time">Время: {{ localTime }}</p>
      <p class="main__content-city">Город: {{ getFullWeather.name }}</p>
      <div class="main__content-img">
        <img src="@/assets/img/sun.svg" alt="" v-if="description == 'ясно'" />
        <img
          src="@/assets/img/cloud.svg"
          alt=""
          v-else-if="description == 'пасмурно'"
        />
        <img
          src="@/assets/img/minirain.svg"
          alt=""
          v-else-if="description == 'небольшой дождь'"
        />
        <img
          src="@/assets/img/sunrain.svg"
          alt=""
          v-else-if="description == 'облачно с прояснениями'"
        />
        <img
          src="@/assets/img/minicloud.svg"
          alt=""
          v-else-if="description == 'небольшая облачность'"
        />
        <img
          src="@/assets/img/minicloud.svg"
          alt=""
          v-else-if="description == 'переменная облачность'"
        />
        <img
          src="@/assets/img/rain.svg"
          alt=""
          v-else-if="description == 'дождь'"
        />
      </div>
    </div>
    <div class="main__content-right">
      <div class="main__content-bg">
        <img src="@/assets/img/headerBg.svg" alt="" />
      </div>
      <div class="main__content-item">
        <div class="main__content-icon">
          <img src="@/assets/img/temp.svg" alt="" />
        </div>
        <span class="main__content-temp">Температура</span>
        <span class="main__content-temp__sub"
          >{{ getFullWeather.current.temp }}° - ощущается как
          {{ getFullWeather.current.feels_like }}°</span
        >
      </div>
      <div class="main__content-item">
        <div class="main__content-icon">
          <img src="@/assets/img/davlenie.svg" alt="" />
        </div>
        <span class="main__content-temp">Давление</span>
        <span class="main__content-temp__sub"
          >{{ getFullWeather.current.pressure }} мм ртутного столба -
          нормальное</span
        >
      </div>
      <div class="main__content-item">
        <div class="main__content-icon">
          <img src="@/assets/img/osadki.svg" alt="" />
        </div>
        <span class="main__content-temp">Осадки</span>
        <span class="main__content-temp__sub">{{
          getFullWeather.current.humidity
        }}</span>
      </div>
      <div class="main__content-item">
        <div class="main__content-icon">
          <img src="@/assets/img/wind.svg" alt="" />
        </div>
        <span class="main__content-temp">Ветер</span>
        <span class="main__content-temp__sub"
          >{{ getFullWeather.current.wind_speed }} м/с юго-запад - легкий
          ветер</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["getFullWeather"]),
    description() {
      return this.getFullWeather.current.weather[0].description;
    },
    localTime() {
      return this.$store.getters.getLocalTime;
    },
  },
};
</script>

<style></style>
